.promo {
    color: $basic-white;
    display: flex;
    justify-content: space-between;
    column-gap: 83px;

    @media (max-width: $tablet-width) {
        padding-left: 65px;
        position: relative;
        column-gap: 0;
    }

    @media (max-width: $mobile-width) {
        padding-left: 0;
        justify-content: center;
    }
}

.promo__info {
    padding-top: 54px;
    padding-left: 37px;

    @media (max-width: $tablet-width) {
        max-width: 61%;
        padding-top: 0;
    }

    @media (max-width: $mobile-width) {
        max-width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        min-height: 280px;
    }
}

.promo__info h1 {
    @include basic-font;
    font-size: 80px;
    font-weight: 700;
    line-height: 94px;
    letter-spacing: 0.07em;
    text-align: left;
    text-transform: uppercase;
    margin: 0;
    margin-bottom: 31px;

    @media (max-width: $tablet-width) {
        font-size: 60px;
        line-height: 70px;
    }

    @media (max-width: $mobile-width) {
        font-size: 40px;
        line-height: 47px;
        margin-bottom: 26px;
    }
}

.promo__info p {
    margin: 0;
    @include add-font;
    font-size: 22px;
    line-height: 34px;
    text-transform: lowercase;
    max-width: 690px;
    position: relative;
    padding-left: 92px;
    margin-bottom: 37px;

    @media (max-width: $tablet-width) {
        max-width: 356px;
        font-size: 18px;
        line-height: 28px;
        padding-left: 48px;
        z-index: 3;
        margin-bottom: 45px;
    }

    @media (max-width: $mobile-width) {
        font-size: 16px;
        line-height: 25px;
        margin-bottom: 40px;
    }

    &::before {
        content: "";
        position: absolute;
        width: 66px;
        height: 2px;
        background-color: $basic-white;
        left: 0;
        top: 15px;

        @media (max-width: $tablet-width) {
            width: 33px;
        }
    }
}

.promo__info a {
    font-size: 16px;
    line-height: 19px;
}

.promo__image {


    @media (max-width: $tablet-width) {
        z-index: -1;
        width: 465px;
        position: absolute;
        margin-left: 60%;
        top: -71px;
    }

    @media (max-width: $mobile-width) {
        width: 100%;
        margin-left: 0;
        top: -183px;
        left: 22px;
    }
}

