@charset "UTF-8";
@font-face {
  font-family: "RobotoCondensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Regular.woff2"), url("../fonts/RobotoCondensed-Regular.woff");
  font-weight: 400;
}
@font-face {
  font-family: "RobotoCondensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Bold.woff2"), url("../fonts/RobotoCondensed-Bold.woff");
  font-weight: 700;
}
@font-face {
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/Roboto-Medium.woff2"), url("../fonts/Roboto-Medium.woff");
  font-weight: 500;
}
@font-face {
  font-family: "PT-Mono";
  font-display: swap;
  src: url("../fonts/PTMono-Regular.woff2"), url("../fonts/PTMono-Regular.woff");
  font-weight: 400;
}
body {
  overflow-x: hidden;
}

.page {
  margin: 0 auto;
  padding: 0;
  min-width: 320px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: #666666;
  background-color: #FFFFFF;
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.container {
  max-width: 1495px;
  margin: 0 auto;
}

.page-main {
  flex-grow: 1;
}

.page-footer {
  flex-shrink: 0;
}

.button {
  font-family: "RobotoCondensed", "Arial", sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  border-radius: 5px;
  color: #FFFFFF;
  text-transform: uppercase;
  font-weight: bold;
  text-align: center;
  cursor: pointer;
  padding: 10px;
  letter-spacing: 0.05em;
  text-decoration: none;
  box-sizing: border-box;
}
.button:hover {
  opacity: 0.8;
}
.button:active {
  opacity: 0.5;
}

.button--consultation {
  display: flex;
  background-color: #00b2ff;
  min-height: 55px;
  width: 100%;
  max-width: 407px;
  margin-left: 92px;
}
@media (max-width: 1023px) {
  .button--consultation {
    max-width: 379px;
    margin-left: 48px;
  }
}
@media (max-width: 767px) {
  .button--consultation {
    display: none;
  }
}

.button--consultation-mobile {
  display: none;
}
@media (max-width: 767px) {
  .button--consultation-mobile {
    display: flex;
    max-width: 100%;
    margin-left: 0;
    background-color: #00b2ff;
    min-height: 55px;
  }
}

.button--more {
  max-width: 180px;
  min-height: 45px;
  color: #00b2ff;
  border: 1px solid #00b2ff;
}

.page-header {
  position: relative;
  min-height: 878px;
  margin-bottom: 109px;
  background-color: #1b2d37;
  background-image: url("../img/bg-header.svg"), url("../img/triangles-promo-desktop.svg");
  background-repeat: no-repeat;
  background-position: top right 14%, center top;
  background-size: cover;
  z-index: 0;
}
@media (min-resolution: 2dppx) {
  .page-header {
    background-image: url("../img/bg-header@2x.svg");
  }
}
@media (max-width: 1023px) {
  .page-header {
    min-height: 685px;
    margin-bottom: 54px;
    background-image: url("../img/triangles-promo-tablet.svg");
    background-position: right top;
    background-size: auto;
  }
}
@media (max-width: 767px) {
  .page-header {
    min-height: 647px;
    margin-bottom: 37px;
    background-image: url("../img/triangles-promo-mobile.svg");
    background-position: right top;
  }
}

.page-header__container {
  padding: 33px 35px 5px;
}
@media (max-width: 1023px) {
  .page-header__container {
    padding: 20px 25px 5px;
  }
}
@media (max-width: 767px) {
  .page-header__container {
    padding: 16px;
  }
}

.page-header__nav {
  max-width: 1410px;
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  margin-bottom: 173px;
}
@media (max-width: 1023px) {
  .page-header__nav {
    margin-bottom: 116px;
  }
}
@media (max-width: 767px) {
  .page-header__nav {
    margin-bottom: 230px;
  }
}

.page-header__contacts {
  display: flex;
  align-items: center;
}

.page-header__contacts-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  column-gap: 88px;
  row-gap: 20px;
}
@media (max-width: 1023px) {
  .page-header__contacts-list {
    column-gap: 57px;
    row-gap: 0;
  }
}
@media (max-width: 767px) {
  .page-header__contacts-list {
    column-gap: 45px;
  }
}

.page-header__contacts-item {
  padding-left: 40px;
  position: relative;
}
@media (max-width: 1023px) {
  .page-header__contacts-item {
    padding-left: 0;
  }
}

.page-header__contacts-link {
  font-family: "PT-Mono", "Arial", sans-serif;
  line-height: 18px;
  color: #FFFFFF;
  text-decoration: none;
}
@media (max-width: 1023px) {
  .page-header__contacts-link {
    font-size: 0;
  }
}
.page-header__contacts-link::before {
  content: "";
  position: absolute;
  left: 0;
  display: flex;
  align-items: center;
  width: 20px;
  height: 20px;
  background-repeat: no-repeat;
  background-position: center;
}
@media (max-width: 1023px) {
  .page-header__contacts-link::before {
    width: 24px;
    height: 24px;
    left: auto;
    right: 0;
  }
}
.page-header__contacts-link--email::before {
  background-image: url("../img/icon-email.svg");
}
@media (max-width: 1023px) {
  .page-header__contacts-link--email::before {
    width: 26px;
    background-size: 24px 19px;
  }
}
.page-header__contacts-link--phone::before {
  background-image: url("../img/icon-phone.svg");
}
@media (max-width: 1023px) {
  .page-header__contacts-link--phone::before {
    background-size: 24px 24px;
  }
}

.page-header__button {
  min-width: 180px;
  min-height: 45px;
  border: 1px solid #FFFFFF;
  font-size: 14px;
  line-height: 16px;
  margin-left: 38px;
}
@media (max-width: 1023px) {
  .page-header__button {
    display: none;
  }
}

.page-header__logo {
  display: block;
  min-width: 202px;
  margin-right: 30px;
}
@media (max-width: 767px) {
  .page-header__logo img {
    width: 38px;
    height: 38px;
  }
}

.promo {
  color: #FFFFFF;
  display: flex;
  justify-content: space-between;
  column-gap: 83px;
}
@media (max-width: 1023px) {
  .promo {
    padding-left: 65px;
    position: relative;
    column-gap: 0;
  }
}
@media (max-width: 767px) {
  .promo {
    padding-left: 0;
    justify-content: center;
  }
}

.promo__info {
  padding-top: 54px;
  padding-left: 37px;
}
@media (max-width: 1023px) {
  .promo__info {
    max-width: 61%;
    padding-top: 0;
  }
}
@media (max-width: 767px) {
  .promo__info {
    max-width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 280px;
  }
}

.promo__info h1 {
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-size: 80px;
  font-weight: 700;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-align: left;
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 31px;
}
@media (max-width: 1023px) {
  .promo__info h1 {
    font-size: 60px;
    line-height: 70px;
  }
}
@media (max-width: 767px) {
  .promo__info h1 {
    font-size: 40px;
    line-height: 47px;
    margin-bottom: 26px;
  }
}

.promo__info p {
  margin: 0;
  font-family: "PT Mono", "Arial", serif;
  font-size: 22px;
  line-height: 34px;
  text-transform: lowercase;
  max-width: 690px;
  position: relative;
  padding-left: 92px;
  margin-bottom: 37px;
}
@media (max-width: 1023px) {
  .promo__info p {
    max-width: 356px;
    font-size: 18px;
    line-height: 28px;
    padding-left: 48px;
    z-index: 3;
    margin-bottom: 45px;
  }
}
@media (max-width: 767px) {
  .promo__info p {
    font-size: 16px;
    line-height: 25px;
    margin-bottom: 40px;
  }
}
.promo__info p::before {
  content: "";
  position: absolute;
  width: 66px;
  height: 2px;
  background-color: #FFFFFF;
  left: 0;
  top: 15px;
}
@media (max-width: 1023px) {
  .promo__info p::before {
    width: 33px;
  }
}

.promo__info a {
  font-size: 16px;
  line-height: 19px;
}

@media (max-width: 1023px) {
  .promo__image {
    z-index: -1;
    width: 465px;
    position: absolute;
    margin-left: 60%;
    top: -71px;
  }
}
@media (max-width: 767px) {
  .promo__image {
    width: 100%;
    margin-left: 0;
    top: -183px;
    left: 22px;
  }
}

.about {
  position: relative;
  max-width: 1220px;
  width: 100%;
  padding: 0 25px 0 205px;
  color: #666666;
  margin-bottom: 80px;
  box-sizing: border-box;
}
@media (max-width: 1023px) {
  .about {
    padding-left: 212px;
    margin-bottom: 60px;
  }
}
@media (max-width: 767px) {
  .about {
    margin-bottom: 50px;
    padding: 0 15px;
  }
}

.about h2 {
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  margin: 0;
  margin-bottom: 18px;
}
.about h2::before {
  content: "";
  position: absolute;
  left: 0;
  background-image: url("../img/year.svg");
  height: 317px;
  width: 220px;
  background-repeat: no-repeat;
  background-size: contain;
}
@media (max-width: 767px) {
  .about h2::before {
    display: none;
    margin-bottom: 12px;
  }
}
@media (max-width: 1023px) {
  .about h2 {
    font-size: 32px;
    line-height: 45px;
    margin-bottom: 25px;
  }
}
@media (max-width: 767px) {
  .about h2 {
    font-size: 28px;
    line-height: 39px;
  }
}

.about p {
  font-family: "PT Mono", "Arial", serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
  text-align: left;
  margin: 0;
  margin-bottom: 15px;
}
@media (max-width: 1023px) {
  .about p {
    margin-bottom: 35px;
  }
}

.about__link {
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  margin-top: 43px;
}

.about__points {
  display: none;
}
@media (max-width: 1023px) {
  .about__points {
    display: inline;
  }
}

@media (max-width: 1023px) {
  .about__desktop {
    display: none;
  }
}

.benefits {
  padding: 0 25px;
  margin-bottom: 110px;
}
@media (max-width: 1023px) {
  .benefits {
    margin-bottom: 54px;
  }
}
@media (max-width: 767px) {
  .benefits {
    padding: 0 16px;
    margin-bottom: 37px;
  }
}

.benefits::before {
  content: "";
  position: absolute;
  background-position: center;
  background-color: #FFFFFF;
  background-image: url("../img/triangles-benefits-desktop.svg");
  background-repeat: no-repeat;
  background-position: left bottom 25px;
  background-size: cover;
  z-index: -1;
  width: 100%;
  height: 1260px;
  margin-left: -25px;
}
@media (max-width: 1023px) {
  .benefits::before {
    background-image: url("../img/triangles-benefits-tablet.svg");
    background-position: right -55px bottom 142px;
    background-size: 665px;
  }
}

.benefits__list {
  margin: 0;
  padding: 0;
  list-style: none;
  max-width: 1220px;
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.benefits__item {
  position: relative;
  border: 1px solid #ebebeb;
  border-left: 3px solid #00b2ff;
  color: #666666;
  background-color: #FFFFFF;
  width: calc(50% - 15px);
  box-sizing: border-box;
  min-height: 300px;
  margin-bottom: 30px;
  display: flex;
  flex-direction: column;
  padding: 40px 45px;
}
@media (max-width: 767px) {
  .benefits__item {
    width: 100%;
    padding: 25px 14px 20px 25px;
    margin-bottom: 20px;
    min-height: auto;
  }
}
.benefits__item:hover {
  background-color: #00b2ff;
  color: #FFFFFF;
}
.benefits__item:hover h2 {
  color: #FFFFFF;
}
.benefits__item:hover svg path {
  fill: #FFFFFF;
}

.benefits__item h2 {
  font-size: 24px;
  line-height: 34px;
  text-align: left;
  font-weight: 400;
  color: #00b2ff;
  margin: 0;
  margin-bottom: 15px;
}
@media (max-width: 767px) {
  .benefits__item h2 {
    font-size: 20px;
    line-height: 28px;
    margin-bottom: 16px;
  }
}

.benefits__item p {
  font-family: "PT Mono", "Arial", serif;
  line-height: 22px;
  text-align: left;
  margin: 0;
}
@media (max-width: 767px) {
  .benefits__item p {
    letter-spacing: -0.02em;
  }
}

.benefits__item svg {
  order: -1;
  margin-bottom: 23px;
}
@media (max-width: 767px) {
  .benefits__item svg {
    margin-bottom: 14px;
  }
}

.catalog {
  background-color: #1b2d37;
  color: #FFFFFF;
  padding: 83px 0 127px;
}
@media (max-width: 1023px) {
  .catalog {
    padding: 53px 25px 78px;
  }
}
@media (max-width: 767px) {
  .catalog {
    padding: 53px 0 64px;
  }
}

.catalog__container {
  max-width: 1220px;
  padding: 0 25px;
  margin: 0 auto;
}
@media (max-width: 767px) {
  .catalog__container {
    padding: 0 15px;
  }
}

.catalog h2 {
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  margin-bottom: 47px;
}
@media (max-width: 1023px) {
  .catalog h2 {
    font-size: 32px;
    line-height: 45px;
  }
}
@media (max-width: 767px) {
  .catalog h2 {
    font-size: 28px;
    line-height: 39px;
    margin: 0 auto 37px;
  }
}

.catalog__list {
  list-style: none;
  padding: 0;
  max-width: 1220px;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 30px;
  row-gap: 30px;
  margin: 0 auto;
  box-sizing: border-box;
}
@media (max-width: 1023px) {
  .catalog__list {
    grid-template-columns: 1fr;
    row-gap: 46px;
  }
}
@media (max-width: 767px) {
  .catalog__list {
    row-gap: 30px;
  }
}

.catalog__item {
  position: relative;
  height: 620px;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 53px 44px;
  box-sizing: border-box;
  cursor: pointer;
}
.catalog__item:hover:before {
  opacity: 1;
}
@media (max-width: 1023px) {
  .catalog__item {
    width: 100%;
    height: 256px;
    padding: 43px;
  }
}
@media (max-width: 767px) {
  .catalog__item {
    padding: 23px 0;
  }
}

.catalog__item::after {
  content: "";
  position: absolute;
  width: 63px;
  height: 63px;
  background-color: #00b2ff;
  background-image: url(../img/icon-arrow.svg);
  background-repeat: no-repeat;
  background-position: center;
  right: 0;
  bottom: 0;
  z-index: 3;
}

.сatalog__item--production::before {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  background-image: url("../img/production.png");
  background-repeat: no-repeat;
  background-position: 50% 0;
  background-size: cover;
  opacity: 0.2;
}
@media (max-width: 1023px) {
  .сatalog__item--production::before {
    background-image: url("../img/production-tablet.png");
  }
}
@media (max-width: 767px) {
  .сatalog__item--production::before {
    background-image: url("../img/production-mobile.png");
  }
}

.сatalog__item--accessories::before {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  background-image: url("../img/accessories.png");
  background-repeat: no-repeat;
  background-position: 50% 0;
  background-size: cover;
  opacity: 0.2;
}
@media (max-width: 1023px) {
  .сatalog__item--accessories::before {
    background-image: url("../img/accessories-tablet.png");
  }
}
@media (max-width: 767px) {
  .сatalog__item--accessories::before {
    background-image: url("../img/accessories-mobile.png");
  }
}

.сatalog__item--mounting::before {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  background-image: url("../img/mounting.png");
  background-repeat: no-repeat;
  background-position: 50% 0;
  background-size: cover;
  opacity: 0.2;
}
@media (max-width: 1023px) {
  .сatalog__item--mounting::before {
    background-image: url("../img/mounting-tablet.png");
  }
}
@media (max-width: 767px) {
  .сatalog__item--mounting::before {
    background-image: url("../img/mounting-mobile.png");
  }
}

.catalog__item a {
  color: inherit;
  text-decoration: none;
  display: block;
  max-width: 155px;
}

.catalog__item h3 {
  margin: 0;
  position: relative;
  z-index: 44;
  font-weight: 400;
}
@media (max-width: 1023px) {
  .catalog__item h3 {
    font-size: 20px;
    line-height: 30px;
  }
}

.feedback-form {
  background-color: #00b2ff;
  color: #FFFFFF;
  padding-top: 95px;
  padding-bottom: 115px;
  position: relative;
  background-repeat: no-repeat;
  background-image: url("../img/triangles-feedback-desktop.svg");
  background-position: right top 57%;
}
@media (max-width: 1023px) {
  .feedback-form {
    padding-top: 83px;
    padding-bottom: 103px;
  }
}
@media (max-width: 767px) {
  .feedback-form {
    padding-top: 33px;
    padding-bottom: 41px;
  }
}

.feedback-form__wrapper::after {
  content: "";
  position: absolute;
  width: 511px;
  height: 288px;
  top: 24%;
  right: 55%;
  background-repeat: no-repeat;
  background-image: url(../img/paper-airplane.svg);
}
@media (max-width: 1023px) {
  .feedback-form__wrapper::after {
    display: none;
  }
}

.feedback-form__container {
  margin: 0 auto;
  max-width: 1220px;
  padding: 0 25px;
  box-sizing: border-box;
}
@media (max-width: 767px) {
  .feedback-form__container {
    padding: 0 15px;
  }
}

.feedback-form__wrapper {
  width: 49%;
  margin-left: auto;
}
@media (max-width: 1023px) {
  .feedback-form__wrapper {
    width: auto;
    margin-left: 0;
  }
}

.feedback-form__wrapper h2 {
  font-size: 36px;
  line-height: 51px;
  font-weight: normal;
  margin: 0;
  margin-bottom: 7px;
}
@media (max-width: 1023px) {
  .feedback-form__wrapper h2 {
    font-size: 32px;
    line-height: 45px;
  }
}
@media (max-width: 767px) {
  .feedback-form__wrapper h2 {
    font-size: 28px;
    line-height: 39px;
    margin-bottom: 17px;
  }
}

.feedback-form__wrapper p {
  font-family: "PT Mono", "Arial", serif;
  font-size: 16px;
  line-height: 22px;
  margin: 0;
  margin-bottom: 50px;
}
@media (max-width: 767px) {
  .feedback-form__wrapper p {
    margin-bottom: 25px;
  }
}

.question-form {
  display: grid;
  align-items: center;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto 1fr auto;
  row-gap: 40px;
  column-gap: 30px;
}
@media (max-width: 767px) {
  .question-form {
    grid-template-columns: 1fr;
    grid-template-rows: auto;
    row-gap: 30px;
  }
}

.question-form textarea {
  grid-column: 1/3;
  color: #FFFFFF;
  background-color: #19baff;
  resize: none;
  border: none;
