.modal {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  display: none;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.modal__wrapper {
  position: relative;
  width: 490px;
  padding: 33px 40px 36px;
  background-color: #00b2ff;
  box-sizing: border-box;
}

.modal h2 {
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-size: 28px;
  font-weight: 400;
  line-height: 50px;
  margin: 0;
  margin-bottom: 6px;
  color: #FFFFFF;
}

.modal p {
  font-family: "PT Mono", "Arial", serif;
  font-weight: 400;
  line-height: 22px;
  display: block;
  width: 370px;
  margin: 0;
  margin-bottom: 27px;
  color: #FFFFFF;
}

.modal__form {
  display: flex;
  flex-direction: column;
}
.modal__form input[type=text],
.modal__form input[type=tel],
.modal__form textarea {
  font-family: "PT Mono", "Arial", serif;
  line-height: 22px;
  margin-bottom: 30px;
  padding: 14px 24px 16px;
  background-color: #19baff;
  outline: none;
  border: none;
  color: white;
}
.modal__form textarea {
  font-family: "PT Mono", "Arial", serif;
  min-height: 104px;
  resize: none;
  box-sizing: border-box;
}

.modal__form input::placeholder,
.modal__form textarea::placeholder {
  color: #FFFFFF;
}

.modal__button {
  width: 100%;
  margin-bottom: 32px;
  padding-top: 14px;
  padding-bottom: 15px;
  letter-spacing: 0.7px;
  color: #FFFFFF;
  border: 1px solid #FFFFFF;
  background-color: #00b2ff;
}

.modal__checkbox {
  display: flex;
  max-width: 360px;
}

.modal__checkbox label {
  font-family: "PT Mono", "Arial", serif;
  position: relative;
  opacity: 0.8;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  cursor: pointer;
  padding-left: 48px;
  color: #FFFFFF;
}

.modal__checkbox label::before {
  content: "";
  position: absolute;
  box-sizing: border-box;
  width: 16px;
  height: 16px;
  left: 5px;
  border: 1px solid #FFFFFF;
}

.modal__button-close {
  font-size: 0;
  position: absolute;
  top: 3px;
  right: 3px;
  width: 50px;
  height: 50px;
  cursor: pointer;
  border: 1px solid transparent;
  outline: none;
  background-color: #00b2ff;
}
.modal__button-close::before, .modal__button-close::after {
  content: "";
  position: absolute;
  top: 13px;
  right: 24px;
  width: 1px;
  height: 23px;
  background-color: #FFFFFF;
}
.modal__button-close::before {
  transform: rotate(45deg);
}
.modal__button-close::after {
  transform: rotate(-45deg);
}
.modal__button-close:hover, .modal__button-close:focus {
  opacity: 0.7;
  border: 1px solid #FFFFFF;
}
.modal__button-close:active {
  opacity: 0.5;
}

.modal--opened {
  display: flex;
  animation: fade-in 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.modal__checkbox input:checked + label::before {
  background-image: url(../img/icon-checkbox.svg);
  background-repeat: no-repeat;
  background-position: center;
}