.catalog {
    background-color: $dark-grey;
    color: $basic-white;
    padding: 83px 0 127px;

    @media (max-width: $tablet-width) {
        padding: 53px 25px 78px;
    }

    @media (max-width: $mobile-width) {
        padding: 53px 0 64px;
    }
}

.catalog__container {
    max-width: 1220px;
    padding: 0 25px;
    margin: 0 auto;

    @media (max-width: $mobile-width) {
        padding: 0 15px;
    }
}

.catalog h2 {
    font-weight: 400;
    font-size: 36px;
    line-height: 51px;
    margin-bottom:  47px;

    @media (max-width: $tablet-width) {
        font-size: 32px;
        line-height: 45px;
    }

    @media (max-width: $mobile-width) {
        font-size: 28px;
        line-height: 39px;
        margin: 0 auto 37px;
    }
}

.catalog__list {
    list-style: none;
    padding: 0;
    max-width: 1220px;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 30px;
    row-gap: 30px;
    margin: 0 auto;
    box-sizing: border-box;

    @media (max-width: $tablet-width) {
        grid-template-columns: 1fr;
        row-gap: 46px;
    }

    @media (max-width: $mobile-width) {
        row-gap: 30px;
    }
}

.catalog__item {
    position: relative;
    height: 620px;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 53px 44px;
    box-sizing: border-box;
    cursor: pointer;

    &:hover:before {
        opacity: 1;
    }

    @media (max-width: $tablet-width) {
        width: 100%;
        height: 256px;
        padding: 43px;
    }

    @media (max-width: $mobile-width) {
        padding: 23px 0;
    }
}

.catalog__item::after {
    content: "";
    position: absolute;
    width: 63px;
    height: 63px;
    background-color: $basic-blue;
    background-image: url(../img/icon-arrow.svg);
    background-repeat: no-repeat;
    background-position: center;
    right: 0;
    bottom: 0;
    z-index: 3;
}


.сatalog__item--production::before {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background-image: url("../img/production.png");
    background-repeat: no-repeat;
    background-position: 50% 0;
    background-size: cover;
    opacity: 0.2;

    @media (max-width: $tablet-width) {
        background-image: url("../img/production-tablet.png");
    } 

    @media (max-width: $mobile-width) {
        background-image: url("../img/production-mobile.png");
    }
}

.сatalog__item--accessories::before {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background-image: url("../img/accessories.png");
    background-repeat: no-repeat;
    background-position: 50% 0;
    background-size: cover;
    opacity: 0.2;

    @media (max-width: $tablet-width) {
        background-image: url("../img/accessories-tablet.png");
    }

    @media (max-width: $mobile-width) {
        background-image: url("../img/accessories-mobile.png");
    }
}

.сatalog__item--mounting::before {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background-image: url("../img/mounting.png");
    background-repeat: no-repeat;
    background-position: 50% 0;
    background-size: cover;
    opacity: 0.2;

    @media (max-width: $tablet-width) {
        background-image: url("../img/mounting-tablet.png");
    }

    @media (max-width: $mobile-width) {
        background-image: url("../img/mounting-mobile.png");
    }
}


.catalog__item a {
    color: inherit;
    text-decoration: none;
    display: block;
    max-width: 155px;
}

.catalog__item h3 {
    margin: 0;
    position: relative;
    z-index: 44;
    font-weight: 400;

    @media (max-width: $tablet-width) {
        font-size: 20px;
        line-height: 30px;
    }
}